body{
  background-color: #9266ff;
  overflow-y: scroll;
}

.App {
  background: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#todo-form input {
  background-color: rgb(14, 97, 122);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: white;
  font-family: monospace;
}

#todo-form input {
  outline: none;
}

#todo-form button {
  height: 50px;
  width: 50px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(252, 185, 0);
  cursor: pointer;
  color: #fff;
  font-weight: bold;
}