body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: #9266ff;
  overflow-y: scroll;
}

.App {
  background: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#todo-form input {
  background-color: rgb(14, 97, 122);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: white;
  font-family: monospace;
}

#todo-form input {
  outline: none;
}

#todo-form button {
  height: 50px;
  width: 50px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(252, 185, 0);
  cursor: pointer;
  color: #fff;
  font-weight: bold;
}
.list {
    color: #fff;
    width: 300px;
    background-color: #bf07ac;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}

.list p {
    padding: 10px;
    font-family: monospace;
    margin: auto;
    position: relative;
    font-size: larger;
}

.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.faicons {
    margin: 0 5px;
    font-size: 15px;
}
